// components/TabsHeadingSection.tsx

import React, { useState } from 'react';

const tabs = ['Food from Shop', 'Food from Farm', 'Food from Restaurant'];

const TabsHeadingSection: React.FC<{
  selected: string;
  onTabChange: (tab: string) => void;
}> = ({ selected, onTabChange }) => {
  return (
    <div className="mb-8">
      <h2 className="text-2xl font-bold mb-4 text-gray-800">
        Discover Delicious Local Food
      </h2>
      <div className="flex space-x-4 border-b">
        {tabs.map((tab) => (
          <button
            key={tab}
            className={`pb-2 text-sm font-medium transition ${
              selected === tab
                ? 'border-b-2 border-black text-black'
                : 'text-gray-500 hover:text-black'
            }`}
            onClick={() => onTabChange(tab)}
          >
            {tab}
          </button>
        ))}
      </div>
    </div>
  );
};

export default TabsHeadingSection;
